@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    background-color: #c1887b;
}

.btn {
    border-radius: 0 !important;
    cursor: pointer;
}

.clicker {
    height: 300px;
    width: 300px;
    margin: 0 auto;
    overflow: hidden;
}

.clicker-display {
    height: 200px;
}

.clicker-button-panel {
    height: 100px;
}

.header {
    height: 100px;
}

.header{
    background-color: #B86C48;
}
* {
    box-sizing: content-box;
}
.container {
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.input-switch {
    display: none;
}

.label-switch-off {
    display: block;
    width: 100px;
    height: 50px;
    background: rgb(181, 189, 189);
    border-radius: 25px;
    border: 3px solid rgb(181, 189, 189);
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
}
.label-switch-off:before {
    display: block;
    background: white;
    content: '';
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: relative;
    left: 0;
    top: 0px;
}
.label-switch-on {
    display: block;
    width: 100px;
    height: 50px;
    background: rgb(57, 180, 180);
    border-radius: 25px;
    border: 3px solid rgb(57, 180, 180);
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
}
.label-switch-on::before {
    display: block;
    background: white;
    content: '';
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: relative;
    left: 50%;
    top: 0px;
}
/* ===================== */
.timer-container {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background: rgb(48, 48, 49);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 10px solid rgb(99, 99, 100);
    color: rgb(57, 180, 180);
}
.timer {
    font-weight: bold;
}
.date {
    font-size: 15px;
}
.date-off {
    display: none;
}

